.form-field {
    margin-bottom: 16px;
    width: 100%;
    position: relative;
}

.form-field .icon {
    position: absolute;
    background: white;
    color: #d61e2d;
    left: 0;
    top: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
    width: 40px;
    height: 40px;
    -webkit-justify-content: center;
            justify-content: center;
    border-radius: 20px;
}

.form-field .icon:after {
    content: "";
    display: block;
    width: 0;
    height: 0;
    border: 12px solid transparent;
    border-left: 12px solid white;
    position: absolute;
    top: 8px;
    right: -20px;
}

.form-field input {
    border: 1px solid rgba(255, 255, 255, 0.2);
    text-align: center;
    width: 100%;
    border-radius: 16px;
    height: 40px;
    background: rgba(255, 255, 255, 0.1);
    color: white;
    outline: none;
    transition: all 0.2s;
}

.form-field input::-webkit-input-placeholder {
    color: white;
}

.form-field input::-ms-input-placeholder {
    color: white;
}

.form-field input::placeholder {
    color: white;
}

.form-field input:hover,
.form-field input:focus {
    background: white;
    color: #d61e2d;
    transition: all 0.2s;
}

.form-field input:hover::-webkit-input-placeholder {
    color: #d61e2d;
}

.form-field input:hover::-ms-input-placeholder {
    color: #d61e2d;
}

.form-field input:hover::placeholder {
    color: #d61e2d;
}
